import React, { Component } from 'react';
import {Modal, Button,} from 'react-bootstrap'
import {
  Col,
  FormGroup,
  Input,
  Select,
  Label,
  Row,
} from 'reactstrap';
import jwt_decode from 'jwt-decode';
// import Spinner from '../../Loaders/Spinner'
import './Settings.scss'
import Server_connections from '../../constants/constants';
import ResetPassword from './ResetPassword'

class ForgotPassword extends Component {
    constructor(props) {
      super(props);
      this.state = {
        id: '',
        old_password: '',
        new_password: '',
        token: '',
        email: '',
        message: '',
        resetPasswordModalShow: false,
        isLoaded: false
      }
      this.handleUpdateItem = this.handleUpdateItem.bind(this);
      this.onChange = this.onChange.bind(this);
    }

    onChange(e){
      this.setState({[e.target.name]: e.target.value});
      //console.log({[e.target.name]: e.target.value});
    }

    handleUpdateItem(e){
      e.preventDefault();

      const forgot_password = {   
        email: this.state.email
      };

      fetch(Server_connections.FORGOT_PASSWORD_URL,{
        method: 'POST',
        headers:{
          'Accept': 'application/json',
          'Content-Type': 'application/json'
        },
        body:JSON.stringify(forgot_password)
      })
      .then(res => res.json())
      .then((result) =>{
        this.setState({message : result.message, isLoaded : true})
        console.log([this.state.message])
        if([this.state.message] == 'Email doesnot exit'){
            
        }
        else if([this.state.message] == 'Email sent'){
            this.setState({resetPasswordModalShow: true})
        }
      },
      (error) => {
        this.setState({message : 'Failed.. Try again!!'})
      });

      const { Email } = this.state;

    }
    
    render() {
        let resetPasswordModalClose = () => this.setState({resetPasswordModalShow : false});
      
        return (
            <Modal {...this.props} size="md" aria-labelledby="contained-modal-title-vcenter" centered>
                <Modal.Header closeButton>
                    <Modal.Title id="contained-modal-title-vcenter"><h5>Forgot Password</h5></Modal.Title>
                </Modal.Header>
                <Modal.Body>
                    <h6><span className='error-msg'>{this.state.message }</span></h6>
                    <FormGroup row className="my-0">
                        <Col xs="12">
                            <FormGroup>
                            </FormGroup>
                        </Col>
                        <Col xs="12">
                            <FormGroup>
                                <Label htmlFor="email" className="lable">Email Address</Label>
                                <Input type="text" name="email" placeholder= "Enter Email Address" value={this.state.email} onChange={this.onChange}/>
                            </FormGroup>
                        </Col>
                        <Col xs="12">
                            <FormGroup>
                            </FormGroup>
                        </Col>
                    </FormGroup>
                </Modal.Body>
                <Modal.Footer>
                {/* <div className="d-inline">
                  <Button block color="danger" size="sm" className="btn-pill bg-danger" onClick={this.props.onHide}>Close</Button>
                </div> */}
                <div className="d-inline">
                  <Button block color="success" size="sm" className="btn-pill bg-success" onClick={this.handleUpdateItem}>Send Mail</Button>
                </div>
              </Modal.Footer>
              <ResetPassword
                email = {this.state.email}
                show = {this.state.resetPasswordModalShow}
                onHide = {resetPasswordModalClose}/>
            </Modal>
        )
    }
}

export default ForgotPassword;
