import axios from 'axios';
import Server_connections from '../../constants/constants';

export const register = newUser => {
    return axios
    .post(Server_connections.ADMIN_SIGNUP_URL, {
        property_name: newUser.property_name,
        landlord_code: newUser.landlord_code,
        email: newUser.email,
        password: newUser.password
    })
    .then(res => {
        console.log("Registered");
        return res.data;
    })
}

export const login = user => {
    return axios
    .post(Server_connections.ADMIN_LOGIN_URL, {
        email: user.email,
        password: user.password
    })
    .then(res => {
        localStorage.setItem('userToken', res.data);
        return res.data;
    })
    .catch(err => {
        console.log(err)
    })
}