import React, { Component } from 'react';
import { Link, withRouter } from 'react-router-dom';
import { AppAsideToggler, AppNavbarBrand, AppSidebarToggler } from '@coreui/react';
import { Button, Card, CardBody, CardGroup, Col, Container, Form, Input, InputGroup, InputGroupAddon, InputGroupText, Row } from 'reactstrap';
import { login } from '../../Pages/UserFunction'
import './Login.scss';
import logo from '../../../assets/img/brand/mispace4.jpg'
import ForgotPassword from '../../Settings/ForgotPassword'

class Login extends Component {
  constructor(props) {
    super(props);
    this.state = { 
        email: '',
        password: '',
        forgotPasswordModalShow: false
    };

    this.onSubmit = this.onSubmit.bind(this);
    this.onChange = this.onChange.bind(this);
  }

  onChange(e){
    this.setState({[e.target.name]: e.target.value});
  }

  onSubmit(e){
    e.preventDefault();

    const user = {
      email: this.state.email,
      password: this.state.password
    }

    login(user).then(res => {
      if(res){
        this.setState({message : res.message, isLoaded : true})
        console.log([this.state.message])
        if([this.state.message] == 'Email or password is incorrect'){
          this.props.history.push('/login')
        }
        else if([this.state.message] == 'User does not exist'){
          this.props.history.push('/login')
        }
        else{
          this.props.history.push('/landlord')
        }
      }else{
        this.props.history.push('/login')
      }
    })
  }

  render() {
    let forgotPasswordModalClose = () => this.setState({forgotPasswordModalShow : false});

    return (
      <div className="app flex-row align-items-center">
        <Container>
          <Row className="justify-content-center">
            <Col md="4">
              <CardGroup>
                <Card className="p-4">
                  <CardBody>
                    <Form>
                      <AppNavbarBrand
                        full={{ src: logo, width: 250, height: 100,  alt: 'CoreUI Logo' }}
                      />
                      <h6 className = 'erro-msg'>{this.state.message }</h6>
                      <hr/>
                      <p className="text-muted">Sign In to your account</p>
                      <InputGroup className="mb-3">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="fa fa-envelope-o"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="text" placeholder="Email" autoComplete="email" name="email"
                        value={this.state.email} onChange={this.onChange}/>
                      </InputGroup>
                      <InputGroup className="mb-4">
                        <InputGroupAddon addonType="prepend">
                          <InputGroupText>
                            <i className="icon-lock"></i>
                          </InputGroupText>
                        </InputGroupAddon>
                        <Input type="password" placeholder="Password" autoComplete="current-password" name="password"
                        value={this.state.password} onChange={this.onChange}/>
                      </InputGroup>
                      <Row>
                        <Col xs="6">
                        <Button color="primary" className="px-4 btn-pill" onClick={this.onSubmit}>Login</Button>
                        </Col>
                        <Col xs="6" className="text-right">
                          <Button color="link" className="px-0" onClick={() => this.setState({forgotPasswordModalShow : true})}>Forgot password?</Button>
                        </Col>
                      </Row>
                    </Form>
                  </CardBody>
                </Card>
                {/* <Card className="text-white bg-primary py-5 d-md-down-none" style={{ width: '44%' }}>
                  <CardBody className="text-center">
                    <div>
                      <h2>Sign up</h2>
                      <p>Lorem ipsum dolor sit amet, consectetur adipisicing elit, sed do eiusmod tempor incididunt ut
                        labore et dolore magna aliqua.</p>
                      <Link to="/register">
                        <Button color="primary" className="mt-3" active tabIndex={-1}>Register Now!</Button>
                      </Link>
                    </div>
                  </CardBody>
                </Card> */}
              </CardGroup>
            </Col>
          </Row>
        </Container>
        <ForgotPassword
          show = {this.state.forgotPasswordModalShow}
          onHide = {forgotPasswordModalClose}/>
      </div>
    );
  }
}

export default withRouter(Login);
